    
.header ul {
    display: flex;
    justify-content: flex-start;
  }
.header {
    display: flex;
}
  .header ul li{
    list-style: none;
  }
  .header ul li a{
    color: #444;
    text-decoration: none;
    margin: 0;
    padding: .5rem 1rem;
    border-radius: 3px;
  }
  
  .header ul li a:hover {
    color:  #fff;
    background-color: #444;
  }
  
  .header a:hover {
    text-decoration: none;
  }
  
  .header h3 a {
    text-transform: uppercase;
    color:  #444;
    text-decoration: none;
    font-size: 27px;
  }
  
  .header h3 a:hover {
    color: #00bc8c;
  }
